* {
  box-sizing: border-box;
}

.body {
  background: #33363d;
  color: white;
  font-family: "Lato";
  font-weight: 300;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

.ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.drag-container {
  max-width: 1000px;
  margin: 20px auto;
  display: flex;
  flex-direction: column;
}

.drag-list {
  display: flex;
  /* align-items: flex-start; */
  flex-direction: column;
}

@media (max-width: 690px) {
  .drag-list {
    display: block;
  }
}

.drag-column {
  flex: 1;
  margin: 10px;
  position: relative;
  background: rgba(0, 0, 0, 0.1);
  overflow: hidden;
  border-radius: 15px;
}

@media (max-width: 690px) {
  .drag-column {
    margin-bottom: 30px;
  }
}

.drag-column h2 {
  font-size: 15px;
  margin: 0;
  text-transform: uppercase;
  font-weight: 600;
  color: white;
}

.drag-column .drag-column-header,
.drag-column .is-moved,
.drag-column .drag-options {
  background: #1c3e6f;
}

.drag-column-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

.student-info-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

.student-info-header h2 {
  font-size: 15px;
  margin: 0;
  text-transform: uppercase;
  font-weight: 600;
  color: #1c3e6f;
}

.student-info {
  text-align: left;
  padding-left: 20px;
  color: grey;
}

.drag-inner-list {
  min-height: 50px;
  list-style-type: none;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.drag-item {
  margin: 5px;
  padding-top: 20px;
  padding-left: 10px;
  padding-right: 10px;
  height: 100px;
  width: 137px;
  background: rgba(0, 0, 0, 0.2);
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  text-align: center;
  display: inline-block;
  border-radius: 5px;
}

.course-name {
  width: 140px;
  margin-top: 30px;
  text-align: center;
  background: none;
  border: none;
  outline: none;
}

.add-course {
  /* margin: 10px; */
  padding-left: 10px;
  display: flex;
  flex-direction: row;
}

.form-input {
  margin: 10px;
}

.form-button {
  width: 25%;
  font-size: 16px;
  color: white;
  background: rgba(0, 0, 0, 0.1);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 10px;
}
.form-button:hover {
  color: #1c3e6f;
}

.notification {
  padding-left: 50px;
  margin-bottom: 15px;
  margin-top: 0px;
  color: red;
}

.section {
  margin-top: 25px;
}

.drag-item.is-moving {
  transform: scale(1.5);
  background: rgba(0, 0, 0, 0.8);
}

.drag-header-more {
  cursor: pointer;
}

.drag-options {
  position: absolute;
  top: 44px;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 10px;
  transform: translateX(100%);
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
}

.drag-options.active {
  transform: translateX(0);
  opacity: 1;
}

.drag-options-label {
  display: block;
  margin: 0 0 5px 0;
}

.drag-options-label input {
  opacity: 0.6;
}

.drag-options-label span {
  display: inline-block;
  font-size: 0.9rem;
  font-weight: 400;
  margin-left: 5px;
}

/* Dragula CSS */
.gu-mirror {
  position: fixed !important;
  margin: 0 !important;
  z-index: 9999 !important;
  opacity: 0.8;
  list-style-type: none;
}

.gu-hide {
  display: none !important;
}

.gu-unselectable {
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}

.gu-transit {
  opacity: 0.2;
}

/* INITIAL FORM */

/* The popup form - hidden by default */
.form-popup {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Add styles to the form container */
.form-container {
  background-color: white;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}
.form-container label {
  margin-left: 20px;
  margin-right: 10px;
}
.form-container select,
option {
  margin-right: 20px;
  margin-bottom: 15px;
  margin-top: 10px;
  padding-left: 10px;
  color: rgba(0, 0, 0, 0.7);
}
/* Set a style for the submit/login button */
.form-container .btn {
  background-color: #1c3e6f;
  color: white;
  padding: 5px 20px;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
}

/* LEGEND */

.student-info-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

.my-legend .legend-title {
  font-size: 15px;
  margin: 0;
  text-transform: uppercase;
  font-weight: 600;
  color: #1c3e6f;
}

.my-legend .legend-scale ul {
  margin-top: 2px;
  padding: 0;
  float: left;
  list-style: none;
  display: flex;
}

.my-legend .legend-scale ul li {
  list-style: none;
  margin-right: 35px;
  line-height: 18px;
}

.my-legend ul.legend-labels li input {
  display: block;
  float: left;
  height: 20px;
  width: 40px;
  margin-right: 5px;
  margin-left: 0;
  border: 0px;
  background-color: #e5e5e5;
}

.column {
  float: left;
  padding: 10px;
}

.left {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

.right {
  width: 75%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 0px;
  padding-top: 20px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.course-core {
  color: #f47c7c;
}
.course-elective {
  color: #70a1d7;
}
.course-minor {
  color: #a1de93;
}
.course-extra {
  color: #f7f48b;
}
