}

.my-legend .legend-scale ul {
  margin-top: 2px;
  padding: 0;
  float: left;
  list-style: none;
  display: flex;
}

.my-legend .legend-scale ul li {
  list-style: none;
  margin-right: 35px;
  line-height: 18px;
}

.my-legend ul.legend-labels li input {
  display: block;
  float: left;
  height: 20px;
  width: 40px;
  margin-right: 5px;
  margin-left: 0;
  border: 0px;
  background-color: #e5e5e5;
}

.column {
  float: left;
  padding: 10px;
}

.left {
  width: 25%;
