@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap");
* {
  font-family: "Fredoka One", cursive;
  letter-spacing: 3px !important;
  color: #1c3e6e;
}

p {
  color: #8198b8;
}

span.taken2 {
  color: rgb(9, 154, 9);
}

.page-content {
  background-color: #f2f4f8;
}

.course-template {
  padding-bottom: 3%;
}

.course-template .course-title {
  padding-top: 5%;
  padding-bottom: 3%;
}

.col-item {
  background-color: white;
  border-radius: 15px;
  margin-right: 10px;
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 2%;
  padding-bottom: 2%;
}

.course-template .syllabus {
  vertical-align: center;
  padding-top: 3%;
  padding-bottom: 3%;
}

.course-template .col-item h3 {
  margin-top: 30px;
  margin-left: 4%;
}

.course-template .col-item h4 {
  margin-top: 5%;
  margin-left: 4%;
}

.course-template .col-item p {
  margin-top: 20px;
  width: 90%;
  margin-left: 4%;
  margin-bottom: 3%;
  color: #5c779c;
}

.course-template .course-description,
.course-requisite {
  margin-left: 0;
}

.requisites-display {
  /* height: 120px; */
  margin-top: 2%;
  background-color: #eff3fb;
  margin-left: 3%;
  border-radius: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-bottom: 4%;
}

.expand_button {
  margin-top: -5%;
}

.syllabus-link {
  margin-top: 2%;
  border-radius: 15px;
  background-color: #1c3e6e;
  color: white;
  width: 8em;
  height: 3em;
  border: none;
  font-size: 1.2em;
  text-align: center;
}

.req-graph {
  border: none;
  width: 60%;
  margin-left: 5%;
  justify-content: center;
  margin-bottom: 1% !important;
  margin-top: 2% !important;
}

.my-legend {
  display: inline-block;
}

.my-legend .legend-scale ul {
  margin: 0;
  margin-bottom: 5px;
  padding: 0;
  float: left;
  list-style: none;
}

.my-legend .legend-scale ul li {
  font-size: 80%;
  list-style: none;
  margin-left: 0;
  line-height: 18px;
  margin-bottom: 2px;
  text-transform: uppercase;
  color: #5c779c;
}

.my-legend ul.legend-labels li span {
  display: block;
  float: left;
  height: 16px;
  width: 30px;
  margin-right: 5px;
  margin-left: 0;
  border: 1px solid #999;
}

.taken {
  background-color: #099a09;
}

.wishlist {
  background-color: #8198b8;
}

#syllabus-row {
  height: 500px;
  display: block;
}

#syllabus-title-row {
  height: 20%;
}

#syllabus-iframe-row {
  margin: 0;
  height: 80%;
}
